#create {
    width: min(30rem, 80vw);
    height: auto;
    margin-top: 1rem;
    border-radius: 1rem;
    border: none;
    box-shadow: 0 0 15px #dbe3eb;
}

#create #container  {
    padding: 5%;
    width: 90%;
}

#in {
    margin-top: 15px;
}

#buttonHolder {
    display: flex;
    margin-top: 1rem;
    color: #f4476b;
}

#button {
    background-color: #f4476b;
    margin-right: 5%;
    border-radius: 1rem;
    border: none;
    color: #f1f7fc;
}

#button:hover {
    background-color: #f55e7e;
}

#edit {
    width: min(30rem, 80vw);
    height: auto;
    border-radius: 1rem;
    border: none;
}

#edit h4 {
    max-width: 75%;
}

#edit textarea {
    max-width: 75% !important;
}

#edit input {
    max-width: 75% !important;
}

#breakBar {
    color: #f4476b;
}

#genButton:hover {
    background-color: #f55e7e;
}

#alertDiv {
    width: min(30rem, 80vw);
}

