#home {
    width: min(30rem, 80vw);
    height: auto;
    margin-top: 15%;
    border-radius: 1rem;
    border: none;
    box-shadow: 0 0 15px #dbe3eb;
    padding: 10%;
}

#home h3 {
    margin: 0;
}

#home p {
    margin-top: .4rem;
}

#home h6 {
    margin-top: .5rem;
}

#home a {
    text-decoration: underline;
    color: inherit;
}